<template>
  <div class="layout">
    <Header></Header>

    <transition name="fade" appear>
      <main class="content">
        <slot></slot>
      </main>
    </transition>

    <Footer></Footer>
  </div>
</template>

<script>
  import Header from '@/layout/Header';
  import Footer from '@/layout/Footer';

  export default {
    name: 'MainLayout',
    data() {
      return {}
    },
    components: {
      Header,
      Footer
    }
  }

</script>

<style lang="scss">
  .content {
    min-height: 100vh;
  }

  .section {
    width: 100%;

    &-container {
      width: 100%;
      max-width: 1060px;
      padding: 120px 30px;
      margin: 0 auto;
      @media only screen and (max-width: 990px) {
        padding: 90px 30px;
      }
      @media only screen and (max-width: 600px) {
        padding: 60px 30px;
      }
    }
  }

  .landing {
    overflow: hidden;

    * {
      box-sizing: border-box;
    }

  }

  .title {
    font-family: 'Roboto', serif;
    font-size: 40px;
    font-weight: 700;
    line-height: 1.3;
    margin-bottom: 30px;

    @media (max-width: 600px) {
      font-size: 30px;
      line-height: calc(40 / 30);
    }

    &-light {
      color: #fff;
    }

    &-default {
      color: #868e9b;
    }

    &-dark {
      color: #002246;
    }

    &-block {
      margin-bottom: 60px;
      text-align: center;
      @media (max-width: 600px) {
        margin-bottom: 30px;
      }
      .title {
        margin-bottom: 5px;
      }
    }
  }

  .subtitle {
    font-size: 20px;
    font-weight: 400;
    line-height: 1.2;

    &-light {
      color: #fff;
    }

    &-default {
      color: #5a6e84;
    }

    &-dark {
      color: #002246;
    }
  }

  .describe {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.6;

    &-light {
      color: #fff;
    }

    &-default {
      color: #868e9b;
    }

    &-dark {
      color: #002246;
    }
  }

  .text {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.3;

    &-light {
      color: #fff;
    }

    &-default {
      color: #5a6e84;
    }

    &-dark {
      color: #002246;
    }
  }

  .button {
    display: flex;
    width: 100%;
    max-width: 280px;
    background: #fff;
    border: 2px solid #162331;
    align-items: center;
    justify-content: center;
    position: relative;
    padding: 15px 10px 15px 10px;
    overflow: hidden;


    &:before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      width: 0;
      height: 0;
      background-color: #162331;
      transition: .3s ease;
    }

    span {
      color: #162331;
      font-size: 14px;
      line-height: 1;
      letter-spacing: 0.15em;
      text-transform: uppercase;
      font-weight: 700;
      position: relative;
      z-index: 5;
      transition: color .3s ease;
    }

    &:hover {
      span {
        color: #fff;
      }
      &:before {
        width: 102%;
        height: 105%;
      }
    }

    /*&:focus, &:active {*/
    /*  box-shadow: 0 4px 10px rgba(28, 28, 28, 0.6);*/
    /*}*/




  }

  /*****************************************************************************/
  /* carousel slider */
  /*****************************************************************************/

  .swiper {
    &-container {
      overflow: hidden !important;
      padding-bottom: 50px;

      > .swiper-pagination {
        &-bullets {
          bottom: -10px !important;
          text-align: left;
        }
      }
    }

    &-pagination {
      &-bullet {
        margin: 0 12px 0 0 !important;
        width: 20px !important;
        height: 30px !important;
        background: transparent !important;
        border-radius: unset !important;
        opacity: 1 !important;
        position: relative;

        &:hover {
          &:before {
            opacity: .7;
          }
        }

        &:before {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
          width: 100%;
          height: 2px;
          transition: opacity .3s ease;
        }

        &-active {
          pointer-events: none;
        }
      }
    }

  }

  .why-us {
    .swiper {
      &-container {
        @media (max-width: 600px) {
          padding-bottom: 0;
        }
      }
      &-wrapper {
        @media (max-width: 600px) {
          flex-direction: column;
        }
      }
      &-pagination-bullet {
        &:before {
          background: #CECDCD;
        }
        &-active {
          &:before {
            background: #fff;
          }
        }
        @media (max-width: 600px) {
          display: none !important;
        }
      }
    }
  }

  .price .swiper {
    &-pagination-bullet {
      &:before {
        background: #CECDCD;
      }
      &-active {
        &:before {
          background: #162331;
        }
      }
      &:before {
        @media (min-width: 1100px) {
          content: none;
        }
      }
    }
  }

  .comments {
    .swiper {
      &-pagination-bullet {
        &:before {
          background: #CECDCD;
        }
        &-active {
          &:before {
            background: #fff;
          }
        }
      }
    }
  }

  .swiper-slide {
    height: auto !important;
  }

</style>



