          s
        </span>
        <img class="header-logotype-lighting" src="../assets/img/layout/header/lightning.svg" alt="">
        <span class="header-logotype-storm glitch" data-text="ORM">
          orm
        </span>
        <img src="../assets/img/layout/header/agency.svg" alt="">
