<script>
  export default {
    name: 'Header',
    data() {
      return {
        isOpenMenu: false,
        isScroll: false,
      }
    },
    mounted() {
      // window.addEventListener('scroll', function(event) {
      // isScroll = window.scrollY > 50;
      // })
      const header = document.getElementById('header');
      window.addEventListener('scroll', function (event) {
        if (window.scrollY > 50) {
          header.classList.add('is-scroll')
        } else {
          header.classList.remove('is-scroll')
        }
      })

    }
  }
</script>

<style lang="scss" scoped>
  .header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 30;
    width: 100%;
    transition: background-color .5s ease;

    &.is-scroll {
      background-color: #002246;

      .header-mobile {
        background-color: #002246;
      }
    }

    &.is-open {

      .header-mobile {
        position: fixed;
        top: 0;
        left: 0;
      }

      .header-content {
        opacity: 1;
        z-index: 55;
        visibility: visible;
      }

      .header-mobile-burger-open {
        opacity: 0;
        visibility: hidden;
      }

