        height: calc(100vh - 80px);
        background: transparent;
        opacity: 0;
        z-index: -5;
        visibility: hidden;
        transition: .3s ease;
        flex-direction: column;
        padding: 0 0 60px;
        justify-content: center;

        .header-logotype {
          display: none;
        }

      }

      &:before {
        content: "";
        position: fixed;
        top: -70px;
        left: 0;
        width: 100%;
        height: calc(100% + 70px);
        background: #002246;
        @media only screen and (min-width: 990px) {
          content: none;
        }
      }

    }

    &-logotype {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      z-index: 5;
      transition: opacity .3s ease;
      font-size: 42px;
      color: #fff;
      letter-spacing: 1px;
      text-transform: uppercase;
      display: flex;
      align-items: center;

      &:hover {
        opacity: .7;
      }

      @media only screen and (max-width: 990px) {
