        width: 100%;
      }
    }

    // link
    &-link {
      font-weight: 700;
      font-size: 14px;
      line-height: (17/14);
      text-transform: uppercase;
      color: #FFFFFF;
      padding: 15px 0;
      margin: 0 15px;
      position: relative;
      overflow: hidden;
      display: block;
      transition: .3s ease;

      @media only screen and (max-width: 990px) {
