        transition: width .3s ease;
        @media only screen and (max-width: 990px) {
          content: none;
        }
      }

      &:hover, &.is-active {
        &:before {
          width: 100%;
        }
        @media only screen and (max-width: 990px) {
          opacity: .7;
        }
      }

      &.write-us {
        display: flex;
        align-items: center;
        padding: 12px 22px;
        background: transparent;
        margin-right: 0;
        border: 2px solid #fff;
        border-radius: 2px;

        @media only screen and (max-width: 990px) {
          margin: 20px 0 0;
          width: 100%;
          max-width: 230px;
          justify-content: center;
        }

        &:before {
          content: none;
        }

        &:after {
          content: "";
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translateX(-50%) translateY(-50%);
          width: 0;
          height: 0;
          background: #fff;
          transition: .3s ease;
        }

        svg {
          margin-left: 6px;
          transition: .3s ease;
          position: relative;
          z-index: 5;

          path {
            transition: .3s ease;
          }

          @media only screen and (max-width: 990px) {
            display: none;
          }
        }

        span {
          position: relative;
          z-index: 5;
          display: block;
          margin-right: 5px;
          color: #fff;
          transition: color .3s ease;
        }

        &:hover, &.is-active {
          opacity: 1;
          span {
