          }
        }

        &-link {
          width: 100%;
          display: flex;
          justify-content: flex-start;
          align-items: center;
          padding: 10px 15px;
          position: relative;

          &:after {
            content: "";
            position: absolute;
            bottom: -0.5px;
            left: 15px;
            width: calc(100% - 30px);
            height: 1px;
            background: rgba(#A8A59E, .2);
          }

          &:first-child {
            padding-top: 15px;
            @media only screen and (max-width: 990px) {
