<template>
  <section class="section comments">
    <div class="section-container">

      <div class="title-block">
        <h3 class="title title-light">Наша репутация -</h3>
        <p class="subtitle subtitle-light">Это отзывы наших клиентов</p>
      </div>

      <swiper ref="swiperComments" :options="swiperCommentsOptions">
        <swiper-slide>
          <div class="comment-slide">
            <div class="comment-slide-header">
              <div class="comment-slide-image">
                <img src="../assets/img/pages/home/comments/avatars/m2.jpg" alt="">
              </div>
              <span class="comment-slide-name">Алексей Баюшкин</span>
            </div>
            <h6 class="comment-slide-title">Спасибо Storm agency</h6>
            <p class="comment-slide-text">Спасибо Storm agency за то, что направили нас в правильное русло, когда мы решили выйти из офлайна в онлайн. Мы активно начали продавать франшизу, открывать новые заведения.</p>
          </div>
        </swiper-slide>
        <swiper-slide>
          <div class="comment-slide">
            <div class="comment-slide-header">
              <div class="comment-slide-image">
                <img src="../assets/img/pages/home/comments/avatars/w1.jpg" alt="">
              </div>
              <span class="comment-slide-name">Валентина Шунько</span>
            </div>

            <h6 class="comment-slide-title">Мы сотрудничаем около года</h6>
            <p class="comment-slide-text">Мы сотрудничаем около года. Хочу поблагодарить как руководителей, так и команду, которая нас сопровождает, за очень эффективную и качественную работу. Спасибо!</p>
          </div>
        </swiper-slide>
        <swiper-slide>
          <div class="comment-slide">
            <div class="comment-slide-header">
              <div class="comment-slide-image">
                <img src="../assets/img/pages/home/comments/avatars/m1.jpg" alt="">
              </div>
              <span class="comment-slide-name">Геннадий Дикий</span>
            </div>
            <h6 class="comment-slide-title">Ребята делают свою работу качественно</h6>
            <p class="comment-slide-text">Я искал команду которая создаст мне качественный контент со своими изюминками. Выбрав команду Storm Agency, ничуть не пожалел. Ребята делают свою работу качественно, оперативно, а главное - с пониманием того что хочет клиент.</p>
          </div>
        </swiper-slide>

        <swiper-slide>
          <div class="comment-slide">
            <div class="comment-slide-header">
              <div class="comment-slide-image">
                <img src="../assets/img/pages/home/comments/avatars/m3.jpg" alt="">
              </div>
              <span class="comment-slide-name">Дмитрий Матросов</span>
            </div>
            <h6 class="comment-slide-title">Мои продажы выросли</h6>
            <p class="comment-slide-text">Storm Agency  - это команда профессионалов, которые помогли в расскрутке моего интернет-магазина одежды, благодаря чему мои продажы выросли.</p>
          </div>
        </swiper-slide>
        <swiper-slide>
          <div class="comment-slide">
            <div class="comment-slide-header">
              <div class="comment-slide-image">
                <img src="../assets/img/pages/home/comments/avatars/w4.jpg" alt="">
              </div>
              <span class="comment-slide-name">Алина Краевая</span>
            </div>
            <h6 class="comment-slide-title">Грамотно выстроена реклама в сетях</h6>
            <p class="comment-slide-text">Когда я решила открыть магазин белья в инстаграмме, то даже подумать не могла о том откуда и как брать клиентов. Но оказалось, все просто зависит от грамотно выстроеннной рекламы в сетях, в чем мне и помогли ребята с Srorm agency, за что я им очень благодарна)</p>
          </div>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>

    </div>
  </section>
</template>

<script>
  import {Swiper, SwiperSlide, directive} from 'vue-awesome-swiper'

  export default {
    name: 'Comments',
    components: {
      Swiper,
      SwiperSlide
    },
    directives: {
      swiper: directive
    },
    data() {
      return {
        swiperCommentsOptions: {
          spaceBetween: 20,
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          },
          breakpoints: {
            1: {
              slidesPerView: 1,
            },
            600: {
              slidesPerView: 2,
            },
            850: {
              slidesPerView: 3,
            },
            990: {
              slidesPerView: 3,
            },
            1100: {
              slidesPerView: 4,
            },
          },
        }
      }
    },
    computed: {
      swiper() {
        return this.$refs.swiperComments.$swiper;
      }
    },
    mounted() {

    }
  }
</script>

<style lang="scss" scoped>
  .comments {
    width: 100%;
    background-color: #099ce7;
    background-image: linear-gradient(to bottom, #002246, #0091ca);
  }

  .comment {
    &-slide {
      width: 100%;
      height: 100%;
      padding: 25px;
      background: #fff;
      position: relative;
      border-radius: 4px;
      overflow: hidden;

      &-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
      }

      &-image {
        width: 69px;
        height: 69px;
        background-image: url("../assets/img/pages/home/comments/avatars/default.svg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        border-radius: 50%;
        overflow: hidden;

        display: flex;
        justify-content: center;
        align-items: center;

        img {
          width: 100%;
          height: 100%;
        }
      }

      &-name {
        width: calc(100% - 85px);
        font-size: 16px;
        font-weight: 500;
        line-height: 1.1;
        color: #162331;
        letter-spacing: -.2px;
      }

      &-title {
        font-size: 22px;
        color: #162331;
        font-weight: 400;
        margin-bottom: 15px;
      }

      &-text {
        font-size: 14px;
        line-height: 1.3;
        color: #5a6e84;
        font-weight: 400;
      }



    }
  }
</style>



