<template>
  <section id="about_us" class="section about-us">
    <div class="section-container">
      <div class="about-us-content">
        <div class="about-us-info">
          <h3 class="title title-dark">
            О сервисе STORM agency
          </h3>
          <p class="describe describe-default">
            Это современное рекламное агентство полного цикла, которое специализируется на решении целого комплекса
            задач, направленных на привлечение новых клиентов в компанию, а также удержание уже имеющихся.
            К вашим услугам многолетний опыт в рекламе, команда профессионалов, надежная репутация и креативный подход к
            маркетинговым задачам любой сложности.
          </p>
        </div>

        <div class="about-us-image">
          <img src="../assets/img/pages/home/about-us/image.jpg" alt="">
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  export default {
    name: 'AboutUs',
    data: function () {
      return {}
    },
    methods: {}
  }
</script>

<style lang="scss" scoped>
  .about-us {
    width: 100%;
    background-color: #fff;

    &-content {
      width: 100%;
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 60px;
      @media only screen and (max-width: 990px) {
        grid-template-columns: 1fr;
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      .title {
        @media only screen and (max-width: 990px) {
          text-align: center;
        }
      }
    }

    &-image {
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      @media only screen and (max-width: 990px) {
        display: none;
      }
      img {
        width: 100%;
        box-shadow: 0 7px 15px #a2b5dc;

      }
    }

  }

</style>



