<template>
  <section class="section hero">
    <div class="landing-container">
      <div class="hero-content">
        <h2 class="hero-title">Интернет-маркетинг для Вашего бизнеса</h2>
        <h4 class="hero-subtitle">Получайте от 125 клиентов в неделю от таргетированной рекламы в <span>Facebook & Instagram</span></h4>
      </div>
    </div>

    <div class="scroll-downs" v-scroll-to="'#about_us'">
      <div class="mousey">
        <div class="scroller"></div>
      </div>
    </div>

    <!-- two layers of Image-->
    <div class="bg">
      <div id="flashit" class="flashit lightning unvisible"></div>
    </div>

  </section>
</template>

<script>
  export default {
    name: 'Hero',
    data() {
      return {}
    }, mounted() {
      const flashit = document.getElementById('flashit');
      setTimeout(function() {
        flashit.classList.remove('unvisible');
      }, 4000);
    }
  }
</script>

<style lang="scss" scoped>
  .hero {
    height: 100%;
    min-height: 100vh;
    width: 100%;
    background-image: url("../assets/img/pages/home/hero/bg.jpg");
    background-position: center center;
    background-origin: content-box;
    background-size: cover;
    background-attachment: fixed;
    z-index: 1;
    position: relative;


    /* Now the same background, but with brightness filters*/
    .lightning{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url("../assets/img/pages/home/hero/bg-light.jpg");
      background-position: center center;
      background-origin: content-box;
      background-size: cover;
      background-attachment: fixed;
      -webkit-filter: brightness(3);
      filter: brightness(3);
      -o-filter: brightness(3);
      -moz-filter: brightness(3);
      z-index: -1;

      @media screen and (max-width: 630px){
        display: none;
      }

      &.flashit{
        -webkit-animation: flash ease-out 7s infinite;
        -moz-animation: flash ease-out 7s infinite;
        animation: flash ease-out 7s infinite;
        animation-delay: 2s;
      }

      @-webkit-keyframes flash {
        from { opacity: 0; }
        92% { opacity: 0; }
        93% { opacity: 0.6; }
        94% { opacity: 0.2; }
        96% { opacity: 0.9; }
        to { opacity: 0; }
      }

      @keyframes flash {
        from { opacity: 0; }
        92% { opacity: 0; }
        93% { opacity: 0.6; }
        94% { opacity: 0.2; }
        96% { opacity: 1; }
        to { opacity: 0; }
      }
    }


    .landing-container {
      padding: 170px 30px 30px;
      position: relative;
      @media only screen and (max-width: 600px) {
        padding: 110px 30px 120px;
      }
    }

    &-content {
      width: 100%;
      position: relative;
      z-index: 15;
    }

    &-title {
      font-family: "Roboto", sans-serif;
      font-weight: 700;
      font-size: 70px;
      line-height: (93/70);
      color: #FFFFFF;
      margin-bottom: 30px;
      text-align: center;

      @media only screen and (max-width: 990px) {
        font-size: 55px;
      }

      @media only screen and (max-width: 600px) {
        font-size: 40px;
        margin-bottom: 30px;
      }

      span {
        font-size: 35px;
        @media only screen and (max-width: 990px) {
          font-size: 32px;
        }
        @media only screen and (max-width: 600px) {
          font-size: 28px;
        }
      }
    }

    &-subtitle {
      font-weight: 700;
      font-size: 18px;
      line-height: 1.2;
      text-align: center;
      color: #9ed9ff;
      margin-bottom: 90px;
    }

    // scroll
    .scroll-downs {
      position: absolute;
      bottom: 150px;
      left: 50%;
      transform: translateX(-50%);
      width :34px;
      height: 55px;
      cursor: pointer;
    }
    .mousey {
      width: 3px;
      padding: 10px 15px;
      height: 35px;
      border: 2px solid #fff;
      border-radius: 25px;
      opacity: 0.75;
      box-sizing: content-box;
    }
    .scroller {
      width: 3px;
      height: 10px;
      border-radius: 25%;
      background-color: #fff;
      animation-name: scroll;
      animation-duration: 2.2s;
      animation-timing-function: cubic-bezier(.15,.41,.69,.94);
      animation-iteration-count: infinite;
    }
    @keyframes scroll {
      0% { opacity: 0; }
      10% { transform: translateY(0); opacity: 1; }
      100% { transform: translateY(15px); opacity: 0;}
    }

  }



</style>



