import Vue from 'vue'
import Router from 'vue-router'
import Homepage from '@/pages/Homepage'
import News from '@/pages/News'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Homepage',
      component: Homepage
    },
    {
      path: '/news',
      name: 'News',
      component: News
    }
  ]
})



