    data() {
      return {
        swiperPriceOptions: {
          spaceBetween: 20,
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
            setWrapperSize: true,
            roundLengths: true
          },
          breakpoints: {
            1: {
              slidesPerView: 1,
              loop: false,
            },
            768: {
              slidesPerView: 2,
            },
            1100: {
              slidesPerView: 3,
            },
