<template>
  <section id="why_us" class="section why-us">
    <div class="section-container">
      <div class="title-block">
        <h3 class="title title-light">Почему выбирают нас?</h3>
        <p class="subtitle subtitle-light">5 причин выбрать STORM agency</p>
      </div>
      <swiper ref="swiperWhyUs" :options="swiperOptions">
        <swiper-slide>
          <div class="why-slide">
            <div class="why-slide-count"><span>1</span></div>
            <div class="why-slide-image">
              <img src="../assets/img/pages/home/why/1.svg" alt="">
            </div>
            <div class="why-slide-content">
              <h5 class="why-slide-title">Анализируем ваш бизнес</h5>
              <p class="text text-default">Выбираем оптимальную стратегию, определяем цели и согласовываем KPI (Ключевой Показатель Эффективности).</p>
            </div>
          </div>
        </swiper-slide>
        <swiper-slide>
          <div class="why-slide">
            <div class="why-slide-count"><span>2</span></div>
            <div class="why-slide-image">
              <img src="../assets/img/pages/home/why/2.svg" alt="">
            </div>
            <div class="why-slide-content">
              <h5 class="why-slide-title">Работа нацелена на результат</h5>
              <p class="text text-default">Мгновенный запуск после оплаты. Редактура объявлений в любое время. Встроенная статистика данных.</p>
            </div>
          </div>
        </swiper-slide>
        <swiper-slide>
          <div class="why-slide">
            <div class="why-slide-count"><span>3</span></div>
            <div class="why-slide-image">
              <img src="../assets/img/pages/home/why/3.svg" alt="">
            </div>
            <div class="why-slide-content">
              <h5 class="why-slide-title">Выбираем инструменты таргетированной рекламы</h5>
              <p class="text text-default">Согласовываем концепцию показа объявлений. Выдвигаем гипотезы, определяем методы и инструменты доставки сообщения аудитории.</p>

            </div>
          </div>
        </swiper-slide>
        <swiper-slide>
          <div class="why-slide">
            <div class="why-slide-count"><span>4</span></div>
            <div class="why-slide-image">
              <img src="../assets/img/pages/home/why/4.svg" alt="">
            </div>
            <div class="why-slide-content">
              <h5 class="why-slide-title">Мы следим за трендами</h5>
              <p class="text text-default">Наши специалисты регулярно посещают профессиональные мероприятия и активно используют новые инструменты в сфере, которая динамично развивается.</p>

            </div>
          </div>
        </swiper-slide>
        <swiper-slide>
          <div class="why-slide is-last">
            <div class="why-slide-count"><span>5</span></div>
            <div class="why-slide-image">
              <img src="../assets/img/pages/home/why/5.svg" alt="">
            </div>
            <div class="why-slide-content">
              <h5 class="why-slide-title">Круглосуточная всесторонняя поддержка</h5>
              <p class="text text-default">Мы работаем в режиме 24/7 и всегда оперативно готовы ответить на любые ваши вопросы.</p>

            </div>
          </div>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </section>
</template>

<script>
  import {Swiper, SwiperSlide, directive} from 'vue-awesome-swiper'

  export default {
    name: 'WhyUs',
    components: {
      Swiper,
      SwiperSlide
    },
    directives: {
      swiper: directive
    },
    data() {
      return {
        swiperOptions: {
          spaceBetween: 20,
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          },
          breakpoints: {
            1: {
              loop: false,
              slidesPerView: 'auto',
              spaceBetween: 0,
              freeMode: true,

            },
            600: {
              slidesPerView: 2,
            },
            850: {
              slidesPerView: 3,
            },
            990: {
              slidesPerView: 3,
            },
            1100: {
              slidesPerView: 4,
            },
          },
        }
      }
    },
    computed: {
      swiper() {
        return this.$refs.swiperWhyUs.$swiper;
      }
    },
    mounted() {

      const swiper = this.$refs.swiperWhyUs.$swiper,
        media = 600;

      window.onresize = swiperMedia();
      window.onload = swiperMedia();
      window.addEventListener('resize', swiperMedia);

      function swiperMedia() {
        if (window.innerWidth <= media) {
          swiper.destroy();
        } else {
          // swiper.init();
        }
      }

    }
  }
</script>

<style lang="scss" scoped>
  .why {
    &-us {
      width: 100%;
      background-color: #099ce7;
      background-image: linear-gradient(to bottom, #002246, #0091ca);
    }


    &-slider {
      width: 100%;

      &-container {
        width: 100%;
        max-width: 1060px;
        padding: 0 30px;
        margin: 0 auto;
        display: flex;
        justify-content: flex-start;
        padding-bottom: 180px;
        position: relative;
        z-index: 5;

        @media only screen and (max-width: 990px) {
          padding-bottom: 90px;
          max-width: 100%;
        }
        @media only screen and (max-width: 600px) {
          padding: 0 20px 15px;
        }
      }

    }

    &-slide {
      height: 100%;
      padding: 50px 30px 25px;
      background: #fff;
      position: relative;
      border-radius: 4px;
      overflow: hidden;

      @media only screen and (max-width: 600px) {
        width: 100%;
        min-height: auto;
        margin-bottom: 20px;
        padding: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
      }

      &.is-last {
        @media only screen and (max-width: 600px) {
          margin-bottom: 0;
        }
      }

      &-count {
        position: absolute;
        top: 0;
        left: 0;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 60px 60px 0 0;
        border-color: #099ce7 transparent transparent transparent;

        span {
          position: absolute;
          top: -50px;
          left: 10px;
          color: #fff;
          font-size: 16px;
          font-weight: 500;
        }
      }

      &-image {
        width: 100%;
        height: 90px;
        position: relative;
        margin-bottom: 15px;
        display: flex;
        justify-content: center;
        align-items: center;
        @media only screen and (max-width: 600px) {
          height: auto;
        }
      }

      &-content {
        @media only screen and (max-width: 600px) {
          width: 100%
        }
      }

      &-title {
        font-weight: 400;
        font-size: 22px;
        line-height: 1.3;
        color: #222222;
        text-align: center;
        margin-bottom: 15px;
        @media only screen and (max-width: 600px) {
          font-size: 18px;
        }
      }
    }

  }

</style>



