<template>
  <footer id="footer" class="footer">
    <div class="footer-container">

      <div class="footer-links">
        <a class="footer-link" v-scroll-to="'#about_us'">О нас</a>
        <a class="footer-link" v-scroll-to="'#why_us'">Почему мы?</a>
        <a class="footer-link" v-scroll-to="'#price'">Цены</a>
      </div>


      <a class="footer-logo" href="#">
        <img src="../assets/img/layout/footer/logotype.svg" alt="footer-logo">
      </a>


    </div>
    <div class="footer-rights">
      STORM agency 2020 Все права защищены
    </div>
  </footer>
</template>

<script>
  export default {
    name: 'Footer'
  }
</script>

<style lang="scss">
  .footer {
    width: 100%;
    background-color: #002246;
    position: relative;

    &-container {
      width: 100%;
      max-width: 1060px;
      padding: 45px 30px;
      margin: 0 auto;
    }

    &-links {
      width: 100%;
      text-align: center;
      margin-bottom: 30px;
    }

    &-link {
      font-weight: 400;
      font-size: 14px;
      line-height: (20/14);
      color: #868e9b;
      transition: .3s ease;
      margin-right: 15px;

      &:hover {
        color: #fff;
      }
    }

    &-logo {
      width: 100%;
      display: flex;
      justify-content: center;

      img {
        transition: opacity .3s ease;
        max-width: 100%;

        &:hover {
          opacity: .7;
        }
      }
    }

    &-rights {
      width: 100%;
      padding: 25px 15px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      z-index: 5;
      font-weight: 400;
      font-size: 14px;
      line-height: (20/14);
      color: #162331;
      background-color: #f1faff;
      text-align: center;
    }

  }

</style>



