<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'App'
  }
</script>

<style lang="scss">

  * {
    box-sizing: border-box;
  }

  // Roboto + Ubuntu
  @import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;200;400;500;700&family=Ubuntu:wght@400;500;700&display=swap');


  html, body {
    max-width: 100%;
    overflow-x: hidden;

    &.no-scroll {
      overflow: hidden !important;
    }
  }

  body {
    font-family: 'Ubuntu', sans-serif;
  }

  a,
  a:link,
  a:visited,
  a:active,
  a:hover {
    text-decoration: none;
  }

  input {
    outline: none;
    border: none;

    &::placeholder,
    &::-webkit-input-placeholder,
    &:-moz-placeholder,
    &:-moz-placeholder,
    &:-ms-input-placeholder {
      color: #A6ABB9;
      opacity: 1;
      transition: opacity 0.3s ease;
    }

    &:focus::placeholder,
    &:focus::-webkit-input-placeholder,
    &:focus::-moz-placeholder,
    &:focus:-moz-placeholder,
    &:focus:-ms-input-placeholder {
      opacity: 0;
      transition: opacity 0.3s ease;
    }
  }

  a, button, input, textarea, span {
    border: none;
    outline: none;
    outline-offset: 0;

    :active, :hover, :focus {
      border: none;
      outline: 0;
      outline-offset: 0;
    }
  }

  a, button {
    cursor: pointer;
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  .hidden {
    display: none !important;
  }

  .unvisible {
    opacity: 0 !important;
  }

  .h-100 {
    height: 100%;
  }

</style>



